.container {
  position: relative;
}

#cart {
  background-color: #ffffff;
  border-color: black;
  border-style: solid;
  border-width: 5px;
  border-radius: 40px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 2;
  padding: 10px;
}

#cart-image {
  background-image: url("../../../public/images/item/cart.png");
  background-size: 100px 100px;
  width: 100px;
  height: 100px;
}

#cart-number {
  font-size: 30px;
  color: white;
  font-weight: bold;
  width: 50px;
  height: 50px;
  border-radius: 30px;
  background-color: red;
  border: none;
}

.item-name {
  font-weight: 600;
  border-radius: 10px;
}

.box {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.5);
}

